html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
  font-family: "proximanova", "Helvetica Neue", "Helvetica", "Roboto", "Arial", sans-serif;
}

body {
  margin: 0;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
  min-width: 100%;
  height: 100%;
  width: 100%;
  color: #333333;
  background-color: white;
}

h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
  margin: 0;
  padding: 0;
}

main,
li {
  display: block;
}

strong {
  font-weight: bold;
}

a,
button {
  color: inherit;
  transition: .3s;
}

a {
  text-decoration: none;
}

button {
  overflow: visible;
  border: 0;
  font: inherit;
  -webkit-font-smoothing: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer;
}

::-moz-focus-inner {
  padding: 0;
  border: 0;
  border-radius: 0;
}

:focus {
  outline: 0;
  box-shadow: none;
  border-radius: 0;
}

img {
  max-width: 100%;
  height: auto;
  border: 0;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 0px;
  border-radius: 0px;
  border: 0;
  border: none;
}

input[type=text] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 0px;
  border-radius: 0px;
}

::-webkit-input-placeholder {
  color: white;
  opacity: 1 !important;
  font-size: 12px !important;
}

:-moz-placeholder {
  /* Firefox 18- */
  color: white;
  opacity: 1 !important;
  font-size: 12px !important;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: white;
  opacity: 1 !important;
  font-size: 12px !important;
}

:-ms-input-placeholder {
  color: white;
  opacity: 1 !important;
  font-size: 12px !important;
}
